/* General */
[data-title] {
    cursor: help;
}

body {
    --primary-color: #2e51a2;
    --primary-color-hover: #203871;
    --secondary-color: #e1e7f5;
    --secondary-color-hover: #bdcae9;
    --watching-color: #2db039;
    --completed-color: #26448f;
    --onhold-color: #f9d457;
    --dropped-color: #a12f31;
	--plantowatch-color: #c3c3c3;
}

.container-fluid {
    -webkit-transition: -webkit-filter 0.15s ease-in-out;
    transition: -webkit-filter 0.15s ease-in-out;
    -o-transition: filter 0.15s ease-in-out;
    transition: filter 0.15s ease-in-out;
    transition: filter 0.15s ease-in-out, -webkit-filter 0.15s ease-in-out;
}

.container-limited {
    max-width: 1400px;
    min-width: 1400px;
}

hr {
    margin: 1rem 0;
	border-top: 1px solid var(--secondary-color);
}

/* Links */
a {
    color: var(--primary-color);
}

a:hover {
    color: var(--primary-color-hover);
}

a[target="_blank"]:after {
    content: 'â†—';
    background: var(--secondary-color);
    display: inline-block;
    margin: 0 0 0 0.25rem;
    padding: 0 0.35rem;
    border-radius: 0.25rem;
    -webkit-transition: background 0.15s ease-in-out;
    -o-transition: background 0.15s ease-in-out;
    transition: background 0.15s ease-in-out;
}

a[target="_blank"]:hover:after {
    background: var(--secondary-color-hover);
}

/* Status pills */
.status-pill {
    padding: 0.25rem 0.5rem;
    border-radius: 0.25rem;
    text-align: center;
}

.status-pill-watching {
    background: var(--watching-color) !important;
    color: #fff;
}

.status-pill-completed {
    background: var(--completed-color) !important;
    color: #fff;
}

.status-pill-onhold {
    background: var(--onhold-color) !important;
    color: #fff;
}

.status-pill-dropped {
    background: var(--dropped-color) !important;
    color: #fff;
}

.status-pill-plantowatch {
    color: hsl(0, 0%, 20%);
    background: var(--plantowatch-color) !important;
}

/* Background colours */
.bg-primary {
    background-color: var(--primary-color-hover) !important;
}

.bg-secondary {
    background-color: var(--secondary-color-hover) !important;
}

/* Forms and buttons */
.btn-group .btn {
    margin: 0 !important;
    padding-left: 0;
    padding-right: 0;
}

.btn-primary {
    background-color: var(--primary-color) !important;
    border-color: var(--primary-color) !important;
}

.btn-primary:hover {
    background-color: var(--primary-color-hover) !important;
    border-color: var(--primary-color-hover) !important;
}

.btn-primary:focus, .btn-primary:active {
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
}

.btn-secondary {
    color: var(--primary-color);
    background-color: var(--secondary-color) !important;
    border-color: var(--secondary-color) !important;
}

.btn-secondary:hover {
    color: var(--primary-color-hover) !important;
    background-color: var(--secondary-color-hover) !important;
    border-color: var(--secondary-color-hover) !important;
}

.btn-secondary:focus, .btn-secondary:active {
    -webkit-box-shadow: none !important;
            box-shadow: none !important;
}

.btn-disabled {
    opacity: 0.5 !important;
    pointer-events: none;
}

.form-control:focus, .custom-select:focus {
    border-color: var(--completed-color);
    -webkit-box-shadow: none;
            box-shadow: none;
}

/* Table */
table {
	table-layout: fixed;
}

tbody tr {
    height: 50px;
}

tbody tr:hover {
    background-color: var(--secondary-color) !important;
    cursor: pointer;
}

tbody tr:hover .link {
    text-decoration: underline;
}

table .progress {
    height: 4px;
    border-radius: 2px;
}

th:not(:first-child) {
	text-align: center;
}

th {
    position: relative;
    padding: 0.75rem 1.5rem 0.75rem 0.75rem !important;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

th:not(:last-child) {
    border-right: 1px solid #dee2e6;
}

th:before {
    content: '';
    height: 0;
    width: 0;
    border: solid transparent;
    border-bottom-color: var(--secondary-color-hover);
    border-width: 5px;
    position: absolute;
    top: 11px;
    right: calc(0.75rem - 5px);
}

th.sort-asc:before {
    border-bottom-color: var(--primary-color);
}

th:after {
    content: '';
    height: 0;
    width: 0;
    border: solid transparent;
    border-top-color: var(--secondary-color-hover);
    border-width: 5px;
    position: absolute;
    bottom: 11px;
    right: calc(0.75rem - 5px);
}

th.sort-desc:after {
    border-top-color: var(--primary-color);
}

th.d-flex {
    display: table-cell !important;
}

td:not(:first-child) {
	text-align: center;
}

td {
    border: none !important;
    position: relative;
}

td:first-child {
    height: 50px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 0.75rem 0 calc(37px + 0.75rem) !important;
}

td:first-child strong {
    background-color: var(--primary-color);
    padding: 0.2rem 0.1rem;
    color: #fff;
    font-weight: normal;
    border-radius: 0.25rem;
}

td.size-column {
	padding: 0.7rem;
}

.anime-image {
    background-size: cover;
    background-position: center;
    position: absolute;
    top: 0;
    left: 0;
    height: 52px;
    width: 37px;
}

.not-downloaded, .size-mismatch {
    background-image: -webkit-linear-gradient(135deg,
     rgba(0, 0, 0, 0.05) 25%,
     transparent 25%,
     transparent 50%,
     rgba(0, 0, 0, 0.05) 50%,
     rgba(0, 0, 0, 0.05) 75%,
     transparent 75%,
     transparent) !important;
    background-image: -o-linear-gradient(135deg,
     rgba(0, 0, 0, 0.05) 25%,
     transparent 25%,
     transparent 50%,
     rgba(0, 0, 0, 0.05) 50%,
     rgba(0, 0, 0, 0.05) 75%,
     transparent 75%,
     transparent) !important;
    background-image: linear-gradient(-45deg,
     rgba(0, 0, 0, 0.05) 25%,
     transparent 25%,
     transparent 50%,
     rgba(0, 0, 0, 0.05) 50%,
     rgba(0, 0, 0, 0.05) 75%,
     transparent 75%,
     transparent) !important;
    background-size: 25px 25px;
}

.d-flex button {
    -ms-flex-positive: 1 !important;
        flex-grow: 1 !important;
}

/* Pagination */
.pagination {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
    margin: 1rem 0;
    text-align: center;
}

.pagination .btn:not(:first-child):not(:last-child), .pagination span {
	display: inline-block;
	height: 2.375rem;
	width: 2.375rem;
	margin: 0 0.25rem;
	padding: 0.375rem 0;
}

/* Ratings gallery */
.ratings-gallery .grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, 100px);
    grid-gap: 1rem;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 1rem 0;
}

.ratings-gallery .grid-item {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.ratings-gallery a {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}

.ratings-gallery .image {
    height: calc(100px * 1.4);
    background-size: cover;
    background-position: center;
}

.ratings-gallery .not-downloaded {
    opacity: 0.3;
    -webkit-transition: opacity 0.15s ease-in-out;
    -o-transition: opacity 0.15s ease-in-out;
    transition: opacity 0.15s ease-in-out;
}

.ratings-gallery .not-downloaded:hover {
    opacity: 1;
}

.ratings-gallery .status-pill {
    padding-left: 0;
    padding-right: 0;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

/* Modal */
@-webkit-keyframes modal-fade-in {
    100% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
@keyframes modal-fade-in {
    100% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

.modal-open .container-fluid {
    -webkit-filter: opacity(0.5) blur(0.5rem);
            filter: opacity(0.5) blur(0.5rem);
}

.modal {
    display: block;
}

.modal-dialog {
    min-width: 1200px;
    max-width: 1200px;
    opacity: 0;
    -webkit-transform: scale(0.75);
        -ms-transform: scale(0.75);
            transform: scale(0.75);
    -webkit-animation: modal-fade-in 0.15s ease-in-out;
            animation: modal-fade-in 0.15s ease-in-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.modal-content {
    border: none;
    -webkit-box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.15),
                0 0.25rem 0.5rem rgba(0, 0, 0, 0.15);
            box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.15),
                0 0.25rem 0.5rem rgba(0, 0, 0, 0.15);
}

.modal .image {
    background-size: cover;
    background-position: center;
    width: 250px;
    height: calc(250px * 1.4);
    margin: 0 auto;
}

.modal p {
    font-size: 0.8rem;
}

.modal .rating-stars {
    font-size: 2rem;
}

.modal .rating-stars .active {
    color: #fcc200;
}

.modal .rating-stars .inactive {
    color: hsl(0, 0%, 75%);
}

.modal .modal-title .status-pill {
    margin: 0 0 0 1rem;
    font-size: 1rem;
    font-weight: normal;
}


